:where(html, body, p, ol, ul, li, dl, dt, dd, blockquote, figure, fieldset, legend, textarea, pre, iframe, hr, h1, h2, h3, h4, h5, h6) {
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
  scrollbar-color: var(--pf-t--global--border--color--default) transparent;
}

::-webkit-scrollbar-thumb {
  background-color: var(--pf-t--global--border--color--default);
  border-radius: var(--pf-t--global--spacer--xs);
}

::-webkit-scrollbar-track {
  background-color: #0000;
}

::-webkit-scrollbar {
  max-width: var(--pf-t--global--spacer--sm);
  max-height: var(--pf-t--global--spacer--sm);
}

:where(html, body) {
  height: 100%;
}

:where(body) {
  font-family: var(--pf-t--global--font--family--body);
  font-size: var(--pf-t--global--font--size--body--default);
  font-weight: var(--pf-t--global--font--weight--body--default);
  line-height: var(--pf-t--global--font--line-height--body);
  color: var(--pf-t--global--text--color--regular);
}

:where(h1, h2, h3, h4, h5, h6) {
  font-size: 100%;
  font-weight: var(--pf-t--global--font--weight--body--default);
}

:where(ul) {
  list-style: none;
}

:where(button, input, optgroup, select, textarea) {
  font-family: inherit;
  font-size: 100%;
  line-height: var(--pf-t--global--font--line-height--body);
  color: var(--pf-t--global--text--color--regular);
  margin: 0;
}

:where(img, embed, iframe, object, audio, video) {
  max-width: 100%;
  height: auto;
}

:where(iframe) {
  border: 0;
}

:where(table) {
  border-spacing: 0;
  border-collapse: collapse;
}

:where(td, th) {
  text-align: start;
  padding: 0;
}

:where(code, pre) {
  font-family: var(--pf-t--global--font--family--mono);
}

:where(a) {
  color: var(--pf-t--global--text--color--link--default);
  text-decoration-line: var(--pf-t--global--text-decoration--link--line--default);
}

:where(a:hover, a:focus) {
  color: var(--pf-t--global--text--color--link--hover);
  text-decoration-line: var(--pf-t--global--text-decoration--link--line--hover);
}

:where(a, button) {
  cursor: pointer;
}

:where(.pf-v6-theme-dark) {
  color-scheme: dark;
}

.pf-v6-screen-reader {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  position: fixed;
  inset-block-start: 0;
  inset-inline-start: 0;
  overflow: hidden;
}

.pf-v6-screen-reader.pf-m-full-size {
  width: 100%;
  height: 100%;
}

.pf-v6-screen-reader.pf-m-absolute {
  position: absolute;
}

.pf-v6-m-tabular-nums {
  font-variant-numeric: tabular-nums;
}

.pf-v6-m-legacy-font {
  --pf-t--global--font--family--body: var(--pf-t--global--font--family--body--legacy);
  --pf-t--global--font--family--heading: var(--pf-t--global--font--family--heading--legacy);
  --pf-t--global--font--family--mono: var(--pf-t--global--font--family--mono--legacy);
  --pf-t--global--font--weight--body: var(--pf-t--global--font--weight--body--legacy);
  --pf-t--global--font--weight--body--bold: var(--pf-t--global--font--weight--body--bold--legacy);
  --pf-t--global--font--weight--heading: var(--pf-t--global--font--weight--heading--legacy);
  --pf-t--global--font--weight--heading--bold: var(--pf-t--global--font--weight--heading--bold--legacy);
}

.pf-v6-m-dir-rtl {
  --pf-v6-global--inverse--multiplier: -1;
  direction: rtl;
}

.pf-v6-m-dir-ltr {
  --pf-v6-global--inverse--multiplier: 1;
  direction: ltr;
}

:where(.pf-v6-m-dir-rtl, [dir="rtl"]) .pf-v6-m-mirror-inline-rtl {
  scale: -1 1;
}

.pf-v6-m-no-motion {
  --pf-t--global--delay--400: 0s !important;
  --pf-t--global--delay--300: 0s !important;
  --pf-t--global--delay--200: 0s !important;
  --pf-t--global--delay--100: 0s !important;
  --pf-t--global--duration--600: 0s !important;
  --pf-t--global--duration--500: 0s !important;
  --pf-t--global--duration--400: 0s !important;
  --pf-t--global--duration--300: 0s !important;
  --pf-t--global--duration--200: 0s !important;
  --pf-t--global--duration--100: 0s !important;
  --pf-t--global--duration--50: 0s !important;
}

